const isRequiredCheck = (data) => {
  let validObj = { valid: true, msg: '' };
  for (const item in data) {
    if (data.hasOwnProperty(item)) {
      const val = data[item];
      if (val === '' || val === undefined || !val || val.length === 0 || val === null) {
        validObj = { valid: false, msg: `${item} is required` };
        return validObj;
      }
      if (item === 'email') {
        const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
        if (!reg.test(data[item])) {
          return { valid: false, msg: 'email address is wrong' };
        }
      }
      if (data.hasOwnProperty('new_password')) {
        if (data.password_verify !== data.new_password) {
          return { valid: false, msg: 'Please keep the same password as your password for confirmation' };
        }
      } else if (item === 'password_verify') {
        if (data.password_verify !== data.password) {
          return { valid: false, msg: 'Please keep the same password as your password for confirmation' };
        }
      }
    }
  }
  return validObj;
};

export default {
  isRequiredCheck,
};
